<template>
	<div class="page-search">
		<div class="page">
			<div class="page-search__filters">
				<input
					type="text"
					autocomplete="off"
					autocapitalize="off"
					v-model="search"
				/>
				<div
					class="page-search__filters-extra"
					:class="{
						'page-search__filters-extra--open': extraFiltersOpen,
					}"
				>
					<button
						v-if="!extraFiltersOpen"
						type="button"
						@click="extraFiltersOpen = true"
					>
						<ui-icon
							src="@/assets/icons/gear"
							size="24"
						/>
					</button>
					<template v-else>
						<ui-checkbox v-model="extraFilters.inCardName">
							Chercher dans le nom de la carte
						</ui-checkbox>
						<ui-checkbox v-model="extraFilters.inAttackOrAbility">
							Chercher dans les attaques et talents
						</ui-checkbox>
						<ui-checkbox v-model="extraFilters.inAttackDescs">
							Chercher dans les descriptions des attaques
						</ui-checkbox>
						<ui-checkbox v-model="extraFilters.all">
							Chercher dans toute les cartes (hors format Standard)
						</ui-checkbox>
						<button
							class="page-search__filters-extra-close"
							@click="extraFiltersOpen = false"
						>
							x
						</button>
					</template>
				</div>
			</div>
			<div>
				<ul>
					<li
						v-for="card of searchResults"
						:key="card.id"
						@click="setCard(card)"
					>
						<span>{{ card.fr.name || card.en.name }}</span>
						<span>{{ card.ext }} {{ card.extId }}</span>
					</li>
				</ul>
			</div>
		</div>
		<div
			v-if="currentCard"
			class="modal-card"
		>
			<span
				class="modal__close"
				@click="currentCard = null"
			>x</span>
			<span
				class="modal__lang"
				@click="toggleLang"
			>{{ cardLanguage }}</span>
			<img
				:src="imgUrlCard"
				@error="cardLanguage = 'en'"
			/>
		</div>
	</div>
</template>

<script setup>
import { computed, watch, onMounted, ref, reactive } from 'vue'
import idb from '@/plugins/idb'
import UiCheckbox from '@/ui/checkbox'

let _timeout

const search = ref('')
const searchResults = reactive([])
const currentCard = ref(null)
const cardLanguage = ref('fr')
const extraFilters = reactive({
	all: false,
	inCardName: true,
	inAttackDescs: false,
	inAttackOrAbility: true,
})
const extraFiltersOpen = ref(false)

const imgUrlCard = computed(() => currentCard.value && `https://pub-4211d6b528d54ed69e8ec217f023723e.r2.dev/${currentCard.value.ext}-${currentCard.value.extId}-${cardLanguage.value}-hd-c.avif`)

onMounted(() => {
	document.querySelector('#app').style.visibility = 'visible'
})

watch(search, val => {
	if (_timeout) return
	_timeout = setTimeout(() => {
		doSearch()
		clearTimeout(_timeout)
		_timeout = null
	}, 150)
})

watch(extraFilters, val => {
	if (_timeout) return
	if (search.value) doSearch()
})

function setCard(_card) {
	currentCard.value = _card
	cardLanguage.value = 'fr'
}
function toggleLang() {
	cardLanguage.value = cardLanguage.value === 'en' ? 'fr' : 'en'
}

async function doSearch() {
	const props = {}
	const keywords = search.value.replace(/((PV|HP)\s+)?([0-9]+)(\s+(PV|HP))?/, (s, ...params) => {
		const [,,hp] = params
		if (hp) props.hp = new RegExp(`^${hp}$`)
		return ''
	}).trim()
	const searchCards = {
		...extraFilters,
		props,
		keywords,
	}
	const results = await idb({ searchCards })
	searchResults.splice(0, searchResults.length, ...results.slice(0, 30))
}
</script>

