<template>
	<label
		:for="id"
		class="ui-checkbox"
	>
		<input
			type="checkbox"
			:id="id"
			:checked="checked"
			@change="toggle"
		/>
		<ui-icon
			v-if="checked"
			src="@/assets/icons/checkbox-checked"
		/>
		<ui-icon
			v-else
			src="@/assets/icons/checkbox"
		/>
		<slot></slot>
	</label>
</template>
<script setup>
import { uuid } from '@common/utils';
const checked = defineModel({ type: Boolean })
const id = uuid('ui-checkbox')

function toggle(ev) {
	checked.value = ev.target.checked
}
console.log(checked.value)
</script>
